@-webkit-keyframes flash {
  0% {
    opacity: .3;
  }
  100% {
    opacity: 1;
  }
}

@keyframes flash {
  0% {
    opacity: .3;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (min-width: 830px) {
  .AboutMe {
    display: flex;
    flex-direction: column;

    &_Main {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
    }

  }
}

.AboutMe {
  //padding: 6.4rem 4.3rem;
  display: flex;
  flex-direction: column;
  background-color: #f8f5d6;


  &_Main {
    display: flex;
    flex-direction: row;
    justify-content: center;
    min-height: 40vh;
    padding: 0 3rem 4.3rem 4.3rem;
    //flex-wrap: wrap;


    &_Left {
      display: flex;
      flex-direction: column;
      padding: 0 4.3rem;
      align-content: center;
      justify-content: center;
      align-items: center;


      h1 {
        font-size: 5.2rem;
        line-height: 1.2;
        letter-spacing: -0.06em;
        margin-bottom: 0.8rem;
        color: #333333;
        text-rendering: optimizelegibility;
        font-weight: normal;
        font-family: "proxima nova bold", "Helvetica Neue", Helvetica, Arial, Sans-serif;
      }

      h2 {


      }

      .intro-wait {
        opacity: 0;
        animation: intro 2s ease 3s forwards;
      }

      .anim {
        width: 0.5rem;
        height: 2.5rem;
        //margin-right: .125rem;
        border-right: solid 2px lightgray;
        white-space: nowrap;
        overflow: hidden;
        word-break: break-all;
      }

      .anim-typewriter2 {
        animation: text-animation2 .8s steps(12) 5.5s 1 normal both, // line 2
        text-animation3 1ms linear 7.6s forwards, // select and delete mistake
        cursor-animation 1000ms steps(40) 5s 6 forwards,
        text-animation4 1s steps(23) 8.5s 1 normal forwards; // last bit of text
        font-size: 1.5rem;
        color: #45465e;
        line-height: 1.3;
        align-content: center;
        align-items: center;
        justify-content: center;
        //display: flex;
        padding-left: 30vw;

      }

      span {
        font-size: 1.5rem;
      }

      @keyframes intro {
        from {
          opacity: 0;
        }
        to {
          opacity: 100%;
        }
      }
      @keyframes text-animation2 {
        from {
          width: 0%;
        }
        to {
          width: 32%;
        }
      }

      @keyframes text-animation3 {
        from {
          width: 38%;
        }
        to {
          width: 18%;
        }
      }

      @keyframes text-animation4 {
        from {
          width: 18%;
        }
        to {
          width: 102%;
        }
      }


      @keyframes selection {
        from {
          background-color: rosybrown;
        }
        to {
          background-color: rgb(51, 153, 255);
        }
      }

      @keyframes delete {
        from {
          font-size: 4rem;
        }
        to {
          font-size: 0;
        }
      }

      .anim-oops {
        animation: selection 1ms linear 7.2s forwards,
        delete 1ms linear 7.6s forwards;
      }

      p {
        font-size: 1.5rem;
        margin-bottom: 2.4rem;
        line-height: 1.2;
        align-content: center;
        color: #45465e;
      }
    }

    &_Right {
      margin-top: 10%;
      //padding: 0 4.3rem;

      img {
        border: 2px solid rgba(10, 10, 10, 0.05);
        border-radius: 5px;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
        opacity: 0.8;
        transition: all ease-in-out 0.3s;
        outline: 1px solid grey;
        padding: 10px 0 -30px 0;
        background-color: rgba(255, 255, 255, 0.6);
        width: 600px;
        height: 450px;
      }

      img:hover {
        cursor: pointer;
        opacity: 1;
        outline: 1px solid #6e6e6e;
        box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.05);
        transition: all ease-in-out 0.3s;
      }
    }
  }

