@-webkit-keyframes flash {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}

@keyframes flash {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}

@keyframes left {
  0% {
    opacity: 0
  }
  50% {
    transform: translateY(200px)
  }
  100% {
    transform: translateY(150px);
    opacity: 1;
  }
}

